"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { FaTrophy, FaBullseye, FaDice } from "react-icons/fa";
import useGameStore from "@/store/gameStore";
import { Player, Game } from "@/types";

export default function StatsPage() {
  const router = useRouter();
  const { players, games } = useGameStore();
  const [selectedPlayer, setSelectedPlayer] = useState<string | "all">("all");
  
  const calculatePlayerStats = (player: Player) => {
    // Get games this player participated in
    const playerGames = games.filter(game => game.playerIds.includes(player.id));
    const completedGames = playerGames.filter(game => game.endedAt);
    
    // Count games won
    const gamesWon = completedGames.filter(game => {
      const playerScores = game.scores.filter(score => score.playerId === player.id);
      const totalScore = playerScores.reduce((sum, score) => {
        let value = score.value;
        if (score.type === "double") value *= 2;
        if (score.type === "triple") value *= 3;
        return sum + value;
      }, 0);
      
      return totalScore === game.startingScore;
    }).length;
    
    // Calculate average score per throw
    const allScores = playerGames.flatMap(game => 
      game.scores.filter(score => score.playerId === player.id)
    );
    
    const totalScoreValue = allScores.reduce((sum, score) => {
      let value = score.value;
      if (score.type === "double") value *= 2;
      if (score.type === "triple") value *= 3;
      return sum + value;
    }, 0);
    
    const avgScorePerThrow = allScores.length > 0 
      ? Math.round(totalScoreValue / allScores.length) 
      : 0;
    
    // Find highest scoring throw
    let highestScoreThrow = 0;
    allScores.forEach(score => {
      let value = score.value;
      if (score.type === "double") value *= 2;
      if (score.type === "triple") value *= 3;
      if (value > highestScoreThrow) {
        highestScoreThrow = value;
      }
    });
    
    return {
      gamesPlayed: playerGames.length,
      gamesCompleted: completedGames.length,
      gamesWon,
      winRate: completedGames.length > 0 ? Math.round((gamesWon / completedGames.length) * 100) : 0,
      totalThrows: allScores.length,
      avgScorePerThrow,
      highestScoreThrow,
    };
  };
  
  const formatDate = (timestamp: number) => {
    return new Date(timestamp).toLocaleDateString();
  };
  
  const getPlayerName = (playerId: string) => {
    const player = players.find(p => p.id === playerId);
    return player ? player.name : "Unknown Player";
  };
  
  const getLatestGames = (limit = 5) => {
    return [...games]
      .filter(game => game.endedAt)
      .sort((a, b) => (b.endedAt || 0) - (a.endedAt || 0))
      .slice(0, limit);
  };
  
  const getPlayerGames = (playerId: string, limit = 5) => {
    return [...games]
      .filter(game => game.playerIds.includes(playerId) && game.endedAt)
      .sort((a, b) => (b.endedAt || 0) - (a.endedAt || 0))
      .slice(0, limit);
  };
  
  const latestGames = selectedPlayer === "all" 
    ? getLatestGames() 
    : getPlayerGames(selectedPlayer);
  
  return (
    <div className="max-w-5xl mx-auto">
      <h1 className="text-3xl font-bold mb-6">Statistik</h1>
      
      {/* Player Filter */}
      <div className="card mb-8">
        <div className="card-body">
          <label className="block font-medium mb-2" htmlFor="player-filter">
            Filtrera efter spelare
          </label>
          <select
            id="player-filter"
            value={selectedPlayer}
            onChange={(e) => setSelectedPlayer(e.target.value)}
            className="w-full md:w-64 px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="all">Alla spelare</option>
            {players.map(player => (
              <option key={player.id} value={player.id}>
                {player.name}
              </option>
            ))}
          </select>
        </div>
      </div>
      
      {/* Player Stats */}
      {selectedPlayer !== "all" && (
        <div className="card mb-8 animate-fade-in">
          <div className="card-header">
            <h2 className="card-title">
              Spelarstatistik: {getPlayerName(selectedPlayer)}
            </h2>
          </div>
          
          {(() => {
            const player = players.find(p => p.id === selectedPlayer);
            if (!player) return <div className="card-body">Spelare hittades inte</div>;
            
            const stats = calculatePlayerStats(player);
            
            return (
              <div className="card-body">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                  <div className="stat-card bg-blue-50">
                    <FaDice className="stat-card-icon text-blue-500" />
                    <h3 className="font-medium text-lg mb-1">Matcher</h3>
                    <div className="stat-card-value">{stats.gamesPlayed}</div>
                    <div className="text-sm text-gray-500 mt-1">
                      {stats.gamesCompleted} avslutade
                    </div>
                  </div>
                  
                  <div className="stat-card bg-green-50">
                    <FaTrophy className="stat-card-icon text-green-500" />
                    <h3 className="font-medium text-lg mb-1">Vinster</h3>
                    <div className="stat-card-value">{stats.gamesWon}</div>
                    <div className="text-sm text-gray-500 mt-1">
                      {stats.winRate}% vinstfrekvens
                    </div>
                  </div>
                  
                  <div className="stat-card bg-red-50">
                    <FaBullseye className="stat-card-icon text-red-500" />
                    <h3 className="font-medium text-lg mb-1">Resultat</h3>
                    <div className="stat-card-value">{stats.avgScorePerThrow}</div>
                    <div className="text-sm text-gray-500 mt-1">
                      genomsnittliga poäng per kast
                    </div>
                  </div>
                </div>
                
                <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="card bg-gray-50">
                    <div className="card-body">
                      <h3 className="font-medium text-lg mb-3">Ytterligare statistik</h3>
                      <ul className="space-y-2">
                        <li className="flex justify-between">
                          <span>Totalt antal kast:</span>
                          <span className="font-medium">{stats.totalThrows}</span>
                        </li>
                        <li className="flex justify-between">
                          <span>Högsta poäng i ett kast:</span>
                          <span className="font-medium">{stats.highestScoreThrow}</span>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            );
          })()}
        </div>
      )}
      
      {/* Recent Games */}
      <div className="card">
        <div className="card-header">
          <h2 className="card-title">
            {selectedPlayer === "all" ? "Senaste matcher" : "Senaste matcher för " + getPlayerName(selectedPlayer)}
          </h2>
        </div>
        
        {latestGames.length === 0 ? (
          <div className="card-body text-center py-10 text-gray-500">
            Inga matcher hittades
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="w-full text-left">
              <thead>
                <tr>
                  <th className="p-4 font-medium">Datum</th>
                  <th className="p-4 font-medium">Spelare</th>
                  <th className="p-4 font-medium">Startpoäng</th>
                  <th className="p-4 font-medium">Varaktighet</th>
                  <th className="p-4 font-medium"></th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {latestGames.map((game: Game) => {
                  const durationMs = (game.endedAt || 0) - game.createdAt;
                  const durationMin = Math.floor(durationMs / 60000);
                  
                  return (
                    <tr key={game.id}>
                      <td className="p-4">{formatDate(game.createdAt)}</td>
                      <td className="p-4">
                        {game.playerIds.map(id => getPlayerName(id)).join(", ")}
                      </td>
                      <td className="p-4">{game.startingScore}</td>
                      <td className="p-4">{durationMin} min</td>
                      <td className="p-4">
                        <button
                          onClick={() => router.push(`/game/${game.id}/summary`)}
                          className="px-3 py-1 bg-blue-600 text-white text-sm rounded-md hover:bg-blue-700 transition-colors"
                        >
                          Visa
                        </button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
} 